<template>
    <section class="pc-container">
        <div class="pcoded-content pt-2 pb-4">
            <!-- <div class="page-header">
                <div class="page-block">
                    <div class="row align-items-center">
                        <div class="col-md-12">
                            <div class="page-header-title">
                                <h5 class="m-b-10">Audiencias</h5>
                            </div>
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <router-link to="/sisge">Inicio</router-link>
                                </li>
                                <li class="breadcrumb-item">Visualizacion</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div> -->
            <!-- <div class="row">
                <div class="col-md-12">
                    <div class="card bg-primary" style="opacity: 0.65;">
                        <div class="card-body">
                            <h4 class="mb-0 text-white">Visualizacion de Audiencias</h4>
                        </div>
                    </div>
                </div>
            </div> -->
            <div class="row py-3">
                <h6 class="text-dark">LISTA DE AUDIENCIAS</h6>
            </div>
            <div class="row">
                <div class="col-xl-12">
                    <div class="card shadow">
                        <div class="card-body ">
                            <div class="table-responsive">
                                <table class="table" id="miTabla">
                                    <thead>
                                        <tr>
                                            <th class="col-md-2">N° de Expediente</th>
                                            <th class="col-md-2">Demandante</th>
                                            <th class="col-md-1">Demandado</th>
                                            <th class="col-md-1">Hora</th>
                                            <th class="col-md-2">Fecha</th>
                                            <th>Lugar</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(audiencia, index) in audiencias" :key="index">
                                            <td>
                                                <router-link
                                                    :to="{ name: 'expediente/detalle', params: { id: audiencia.exp_id } }">{{
                                                        audiencia.exp_numero }}</router-link>
                                            </td>
                                            <td  v-if="audiencia.procesal === 'demandante'">
                                                <router-link v-if="audiencia.tipo_persona === 'Natural'"
                                                    :to="{ name: 'demandante/informacion', params: { doc: audiencia.nat_dni } }">{{
                                                        demandanteNombre(audiencia) }}</router-link>
                                                <span v-else>{{ audiencia.jur_razon_social }}</span>
                                            </td>
                                            <td>UNPRG</td>
                                            <td  v-if="audiencia.procesal === 'demandado'">
                                                <router-link v-if="audiencia.tipo_persona === 'Natural'"
                                                    :to="{ name: 'demandante/informacion', params: { doc: audiencia.nat_dni } }">{{
                                                        demandanteNombre(audiencia) }}</router-link>
                                                <span v-else>{{ audiencia.jur_razon_social }}</span>
                                            </td>

                                            <td>{{ audiencia.aud_hora }}</td>
                                            <td>{{ audiencia.aud_fecha }}</td>
                                            <td>{{ audiencia.aud_lugar }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
  
<script>
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import feather from 'feather-icons';

import { mapActions } from 'vuex';

export default {
    name: 'audienciaAll',
    created() {
        this.listarAudiencias();
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            audiencias: [],
            dataTable: null,
        };
    },
    methods: {
        ...mapActions(['get', 'post']),
        async listarAudiencias() {
            try {
                const response = await this.get({
                    url: this.$store.getters.get__url + '/audiences',
                    token: this.$store.getters.get__token,
                });
                console.log(response);
                this.audiencias = response.data;
                this.initDataTable();
            } catch (error) {
                console.error(error);
            }
        },
        initDataTable() {
            if (this.dataTable) {
                this.dataTable.destroy();
            }
            this.$nextTick(() => {
                this.dataTable = $('#miTabla').DataTable({
                    language: {
                        url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
                    },
                });
            });
        },
        demandanteNombre(audiencia) {
            return audiencia.nat_apellido_paterno + ' ' + audiencia.nat_apellido_materno + ' ' + audiencia.nat_nombres;
        },
    },
    beforeDestroy() {
        if (this.dataTable) {
            this.dataTable.destroy();
        }
    },
};
</script>
  
<style scoped>
td {
    white-space: normal !important;
    word-wrap: break-word;
}
</style>
  